 from{\n        left: -45%\n    }\n    to{\n        left: -100%\n    }\n}\n@keyframes openTheDoorRight{\n    from{\n        right: -45%\n    }\n    to{\n        right: -100%\n    }\n}\n#left_door{\n    .door;\n    background-color: black;\n    left: -100%;\n    clip-path: polygon(0 0, 100% 0, \n    90% (100%/9*1), 100% (100%/9*2), 90% (100%/9*3),\n    100% (100%/9*4), 90% (100%/9*5), 100% (100%/9*6),\n    90% (100%/9*7), 100% (100%/9*8),\n    90% 100%, 0 100%);\n}\n#left_door::before{\n    content: \"\";\n    .door;\n    right: 10px;\n    z-index: 999;\n    clip-path: polygon(0 0, 100% 0, \n    90% (100%/9*1), 100% (100%/9*2), 90% (100%/9*3),\n    100% (100%/9*4), 90% (100%/9*5), 100% (100%/9*6),\n    90% (100%/9*7), 100% (100%/9*8),\n    90% 100%, 0 100%);\n    background-image: url(../images/door.jpg);\n}\n#right_door{\n    .door;\n    background-color: black;\n    right: -100%;\n    clip-path: polygon(10% 0, 100% 0,\n    100% 100%, 0 100%, \n    10% (100%/9*8), 0 (100%/9*7), 10% (100%/9*6),\n    0 (100%/9*5), 10% (100%/9*4), 0% (100%/9*3),\n    10% (100%/9*2), 0 (100%/9*1)\n    );\n}\n#right_door::before{\n    content: \"\";\n    .door;\n    width: 97%;\n    left: 10px;\n    z-index: 999;\n    clip-path: polygon(10% 0, 100% 0,\n    100% 100%, 0 100%, \n    10% (100%/9*8), 0 (100%/9*7), 10% (100%/9*6),\n    0 (100%/9*5), 10% (100%/9*4), 0% (100%/9*3),\n    10% (100%/9*2), 0 (100%/9*1)\n    );\n    background-image: url(../images/door.jpg);\n}","//spash screen\r\n@header-font-color: #fffedb;\r\n\r\n@main-button-background-color: rgb(231, 15, 15);\r\n@main-button-font-color: #fffee0;\r\n@main-button-hover-background-color: #3fb007;\r\n@main-button-hover-font-color: #ede618;\r\n\r\n@switchsound-button-background-color: #a3fff6;\r\n\r\n@clockTime-color: rgb(187, 218, 50);\r\n\r\n//game board\r\n@card-background-color: repeating-linear-gradient(150deg, rgb(227, 228, 228) 5%, rgb(127, 169, 233) 8%);\r\n@score-stats-font-color: rgb(10, 148, 212);\r\n@score-squinting-stats-font-color: rgb(204, 40, 34);\r\n\r\n//end screen\r\n@end-message-text-shadow-color: 2px 2px 4px rgb(231, 208, 208);\r\n@optionAndEnd-button-background-color: rgb(9, 131, 231);\r\n@optionAndEnd-button-hover-background-color: rgb(12, 112, 170);\r\n\r\n.buttons{\r\n    position: absolute;\r\n    bottom: 15%;\r\n    width: 100%;\r\n    justify-content: space-around;\r\n    button{\r\n        .button\r\n    }\r\n}\r\n.button{\r\n    display: inline-block;\r\n    background-color: @optionAndEnd-button-background-color;\r\n    padding: 10px 30px 15px;\r\n    border-radius: 22px;\r\n    color: white;\r\n    font-size: 2.5rem;\r\n    border: none;\r\n    outline: none;\r\n    font-family: inherit;\r\n    min-width: 250px;\r\n    &:hover{\r\n        cursor: pointer;\r\n        background-color: @optionAndEnd-button-hover-background-color;\r\n    }\r\n}\r\n\r\n.option{\r\n    display: inline-block;\r\n    width: 120px;\r\n    height: 120px;\r\n    position: relative;\r\n    cursor: pointer;\r\n    margin: 0 10px;\r\n    border-radius: 6px;\r\n    opacity: .55;\r\n}","@import url('https://fonts.googleapis.com/css2?family=Stick&display=swap');\n@import \"./door.less\";\n@import \"./variables.less\";\n\n.header-font{\n    color: @header-font-color;\n    letter-spacing: 5px;\n}\n#game_start{\n    // display: none;\n    text-align: center;\n    font-size: 1.5em;\n    opacity: .9;\n    width: 70%;\n    margin: auto;\n    padding-top: 30px;\n    header{\n        .header-font;\n        & *{\n            margin: 4px auto;\n        }\n    }\n    section.main_buttons{\n        margin: 90px 0;\n        & .main_button{\n            margin: 50px auto;\n            padding: 20px;\n            background-color: @main-button-background-color;\n            color: @main-button-font-color;\n            width: 70%;\n            transform: skew(-15deg);\n            font-size: 1.5rem;\n            cursor: pointer;\n            &:hover{\n                background-color: @main-button-hover-background-color;\n                color: @main-button-hover-font-color;\n            }\n            span{\n                display: inline-block;\n                transform: skew(15deg);\n            }\n        }\n    }\n    .switch_sound{\n        position: absolute;\n        bottom: 30px;\n        right: 50px;\n        background-color: @switchsound-button-background-color;\n        padding: 10px 10px 5px;\n        cursor: pointer;\n        img{\n            width: 32px;\n        }\n    }\n    \n    \n}\n\n@media all and (orientation: landscape) and (max-device-width: 800px){\n    #game_start{\n        header{\n            font-size: 15px;\n        }\n        section.main_buttons{\n            margin: 50px 0;\n            & .main_button{\n                margin: 30px auto;\n                padding: 10px;\n                font-size: 1.3rem;\n                width: 90%;\n            }\n        }\n    }\n}\n@media all and (orientation: portrait) and (max-device-width: 800px){\n    #game_start{\n        header{\n            font-size: 1.2em;\n        }\n        section.main_buttons{\n            & .main_button{\n                margin: 55px auto;\n                padding: 20px;\n                font-size: 1.9rem;\n                width: 90%;\n            }\n        }\n        .switch_sound{\n            position: absolute;\n            bottom: 30px;\n            right: 50px;\n            background-color: @switchsound-button-background-color;\n            padding: 10px 10px 5px;\n            cursor: pointer;\n            img{\n                width: 64px;\n            }\n        }\n    }\n}","@font-face {\n  font-family: 'Indie Flower';\n  font-style: normal;\n  font-weight: 400;\n  font-display: swap;\n  src: url(https://fonts.gstatic.com/s/indieflower/v12/m8JVjfNVeKWVnh3QMuKkFcZlbg.ttf) format('truetype');\n}\n","@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap');\n@import \"./variables.less\";\n#game_board{\n    display: none;\n    width: 100%;\n    height: 100%;\n    position: relative;\n    #game_cards{\n        width: 80%;\n        position: absolute;\n        top: 47%;\n        left: 50%;\n        transform: translate(-50%, -50%);\n        text-align: center;\n        .card_background{\n            display: inline-block;\n            margin: 5px;\n            border: 5px solid black;\n            border-radius: 5px;\n            box-sizing: border-box;\n            text-align: center;\n            transition: all .3s linear;\n            filter: brightness(70%);\n            cursor: pointer;\n            background-size: cover;\n            background-image: @card-background-color;\n            // width: 170px;\n            // height: 170px;\n            &:hover{\n                filter: brightness(90%);\n            }.card{\n                img{\n                    width: 100%;\n                    height: 100%;\n                } \n            }  \n        }\n        \n    }\n    .stats{\n        color: @score-stats-font-color;\n        font-size: 4em;\n        font-family: 'Indie Flower', cursive;\n        font-weight: 700;\n        position: absolute;\n        text-align: center;\n        text-shadow: 2px 2px 2px whitesmoke;\n        filter:blur(.5px);\n        line-height: 85%;\n        #scoreCounter{\n            font-size: 5rem;\n        }\n    }\n    #score{\n        .stats;\n        bottom: 22%;\n        left: 1%;\n    }\n    #timer{\n        display: none;\n        .stats;\n        bottom: 21%;\n        right: 2%;\n    }\n}\n@media all and (min-device-width: 801px){\n    #game_board{\n        #game_cards{\n            .card_background{\n                // width: 110px !important;\n                // height: 110px !important;\n            }\n        }\n    }\n}\n@media all and (orientation: landscape) and (max-device-width: 800px){\n    #game_board{\n        #game_cards{\n            width: 82%;\n            top: 42%;\n            .card_background{\n                // width: 93px !important;\n                // height: 93px !important;\n                // margin: 2px;\n                border: 3px solid black;\n            }\n        }\n        .stats{\n            #scoreCounter{\n                font-size: 3.5rem;\n            }\n        }\n        #score{\n            .stats;\n            font-size: 3em;\n            bottom: 2%;\n            left: 40px;\n        }\n        #timer{\n            font-size: 3em;\n            bottom: 2%;\n            right: 40px;\n        }\n    }\n}\n\n@media all and (orientation: portrait) and (max-device-width: 800px){\n    #game_board{\n        #game_cards{\n            width: 97%;\n            top: 42%;\n        }\n        .stats{\n            #scoreCounter{\n                font-size: 2rem;\n            }\n        }\n        #score{\n            font-size: 5em;\n            bottom: 2%;\n            left: 110px\n        }\n        #timer{\n            font-size: 5em;\n            bottom: 2%;\n            right: 110px;\n        }\n    }\n}","@font-face {\n  font-family: 'RocknRoll One';\n  font-style: normal;\n  font-weight: 400;\n  font-display: swap;\n  src: url(https://fonts.gstatic.com/s/rocknrollone/v3/kmK7ZqspGAfCeUiW6FFlmEC9guU.ttf) format('truetype');\n}\n","@font-face {\n  font-family: 'Orbitron';\n  font-style: normal;\n  font-weight: 700;\n  font-display: swap;\n  src: url(https://fonts.gstatic.com/s/orbitron/v17/yMJMMIlzdpvBhQQL_SC3X9yhF25-T1ny_Cmxpg.ttf) format('truetype');\n}\n","@import url('https://fonts.googleapis.com/css2?family=RocknRoll+One&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@700&display=swap');\n@import \"./variables.less\";\n\n\n#game_options{\n    display: block;\n    display: none;\n    width: 90%;\n    margin: 100px auto;\n    .option_step{\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        border: 7px solid rgb(194, 145, 117);\n        border-radius: 20px;\n        backdrop-filter: brightness(160%);\n        padding: 12px;\n        margin: 40px auto;\n        .option_description{\n            display: inline-block;\n            width: 43%;\n            text-align: center;\n            font-size: 2.2em;\n        }\n        .options{\n            display: inline-flex;\n            justify-content: flex-start;\n            align-items: center;\n            width: 65%;\n            .option{\n                .option;\n                border: 7px solid black;\n                background-image: @card-background-color;\n                img{\n                    width: 100%;\n                }\n                .optHint{\n                    position: absolute;\n                    bottom: -10px;\n                    right: -10px;\n                    font-size: 2.7em;\n                    font-weight: 700;\n                    color: rgb(179, 118, 84);\n                    font-family: 'RocknRoll One', sans-serif;\n                    text-shadow: 2px 2px 2px rgb(0, 0, 0);\n                }\n            }\n            .timerNo{\n                .option;\n                background-image: none;\n                background-color: rgba(241, 47, 47, 0.8);\n            }\n            .timerYes{\n                .option;\n                background-image: none;\n                background-color: rgba(96, 212, 96, .8);\n            }\n            .clock{\n                position: relative;\n                width: 265px;\n                display: none;\n                img{\n                    width: 100%;\n                }\n                .clockTime{\n                    width: 100%;\n                    text-align: center;\n                    position: absolute;\n                    top: 55%;\n                    left: 50%;\n                    transform: translate(-50%, -50%);\n                    font-size: 2.5rem;\n                    font-family: 'Orbitron', sans-serif;\n                    letter-spacing: 1.2px;\n                    color: @clockTime-color;\n                    .clockTimeDisplay{\n                        width: 56%;\n                        display: inline-block;\n                        margin: auto;\n                    }\n                    .updateTime{\n                        width: 7%;;\n                        font-size: 2.5rem;\n                        display: inline-block;\n                        margin: auto;\n                        & button{\n                            background-color: transparent;\n                            outline: none;\n                            border: none;\n                            color: inherit;\n                            font-size: 100%;\n                            font-weight: 700;\n                            cursor: pointer;\n                        }\n                    }\n                }\n            }\n            .active{\n                border: 7px solid rgb(255, 187, 0);\n                opacity: 1;\n            }\n        }\n    }\n    .buttons{\n        .buttons;\n        bottom: 10%;\n        right: 0;\n        text-align: center;\n        button{\n            font-size: 1.8em;\n        }\n    }\n}\n@media all and (orientation: landscape) and (max-device-width: 800px){\n    #game_options{\n        margin-top: 30px;\n        .option_step{\n            .option_description{\n                font-size: 1.6em;\n                width: 20%;\n                text-align: left;\n            }\n            .options{\n                width: 80%;\n                .option{\n                    width: 92px !important;\n                    height: 92px !important;\n                    .optHint{\n                        font-size: 1.6em;\n                    }\n                }\n                .clock{\n                    width: 170px;\n                    .clockTime{\n                        font-size: 1.5rem;\n                        .updateTime{\n                            font-size: 1.8rem;\n                        }\n                    }\n                }\n            }\n        }\n    }\n}\n@media all and (orientation: portrait) and (max-device-width: 800px){\n    #game_options{\n        .option_step{\n            margin: 100px auto;\n            .option_description{\n                width: 20%;\n                text-align: left;\n            }\n            .options{\n                width: 70%;\n                .clock{\n                    width: 260px;\n                }\n            }\n        }\n        .buttons{\n            button{\n                font-size: 2.8em;\n            }\n        }\n    }\n}","#game_bestScores{\n    display: none;\n}","@import \"./variables.less\";\n\n\n#game_end{\n    display: none;\n    text-align: center;\n    font-size: 3em;\n    position: relative;\n    width: 100%;\n    height: 100%;\n    .wrapper{\n        width: 100%;\n        height: 100%;\n        .message{\n            font-size: 2.8rem;\n            margin-top: 80px;\n            height: 100%;\n            .message_lose{\n                font-size: 3.3em;\n                padding-top: 10px;\n                text-align: center;\n                color: red;\n                display: none;\n            }\n            .message_statistics{\n                // background-color: red;\n                min-height: 50%;\n                .message_segment{\n                    display: none;\n                    justify-content: space-between;\n                    align-items: center;\n                    width: 90%;\n                    margin: auto;\n                    color: green;\n                    &#timeStart, &#madeMoves{\n                        color: red;\n                    }\n                    \n                    .message_segment_description{\n                        width: 40%;\n                    } \n                    .message_segment_score{\n                        width: 57%;\n                    }\n                }\n            }\n            \n            #totalPoints{\n                display: none;\n                margin-top: 20px;\n                // position: absolute;\n                // bottom: 40%;\n                font-size: 4.5rem;\n                width: 90%;\n                text-align: center;\n                .message_segment_description{\n                    width: 40%;\n                } \n                .message_segment_score{\n                    width: 50%;\n                }\n            }\n            \n            text-align: left;\n            margin: 30px;\n            text-shadow: @end-message-text-shadow-color;\n        }\n        .video{\n            position: absolute;\n            top: 45%;\n            left: 50%;\n            transform: translate(-50%, -50%);\n            width: 700px;\n            margin: 0 auto;\n            video{\n                width: 100%;\n                border: 5px solid black;\n                border-radius: 15px;\n            }\n        }\n        .buttons{\n            .buttons\n        }\n    }\n\n}\n@media all and (orientation: landscape) and (max-device-width: 800px){\n    #game_end{\n        .wrapper{\n            .message{\n                font-size: 1.8rem;\n                margin: 30px 10px 0;\n                .message_lose{\n                    font-size: 3.1em;\n                }\n                .message_segment{\n                    margin-top: 15px;\n                    width: 100%;\n                    &#totalPoints{\n                        bottom: 36%;\n                        font-size: 3.5rem;\n                        .message_segment_description{\n                            width: 53%;\n                        } \n                        .message_segment_score{\n                            width: 71%;\n                        }\n                    }\n                }\n            }\n            .video{\n                width: 95%;\n            }\n            .buttons{\n                bottom: 15%;\n                .button{\n                    padding: 0;\n                    button{\n                        font-size: .75em;\n                    }\n                }\n            }\n        }\n    }\n}\n@media all and (orientation: portrait) and (max-device-width: 800px){\n    #game_end{\n        font-size: 7em;\n        .wrapper{\n            .message{\n                padding-top: 50px;\n                font-size: 2.5rem;\n                .message_segment{\n                    margin-top: 20px;\n                    width: 95%;\n                }\n            }\n        }\n    }\n}","@import \"./variables.less\";\n\n@keyframes show{\n    from{opacity: 0;}\n    to{opacity: 1;}\n}\n@keyframes hide{\n    from{opacity: 1;}\n    to{opacity: 0;}\n}\n@keyframes squintingTimer{\n    from {color: inherit}\n    to {color: @score-squinting-stats-font-color}\n}\n@keyframes bestScore{\n    from {\n        font-size: inherit;\n    }\n    to{\n        font-size: 5rem;\n        bottom: 90%;\n        left: 50%;\n        transform: translate(-50%, -50%);\n    }\n}"],"sourceRoot":""}]);
